// wishlistService.js
import { ref } from 'vue';

// ÏÉÅÌÉú Í¥ÄÎ¶¨ - wishlistÎ•º Ï†ÄÏû•Ìï† ref ÏÉÅÌÉú Î≥ÄÏàò
const wishlist = ref([]);

// Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏúÑÏãúÎ¶¨Ïä§Ìä∏ Î°úÎìú
function loadWishlist() {
  const storedWishlist = localStorage.getItem('movieWishlist');
  if (storedWishlist) {
    wishlist.value = JSON.parse(storedWishlist);
  }
}

// Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏúÑÏãúÎ¶¨Ïä§Ìä∏ Ï†ÄÏû•
function saveWishlist() {
  localStorage.setItem('movieWishlist', JSON.stringify(wishlist.value));
}

// ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏòÅÌôî Ï∂îÍ∞Ä/Ï†úÍ±∞
export function toggleWishlist(movie) {
  const index = wishlist.value.findIndex(item => item.id === movie.id);
  if (index === -1) {
    // ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏóÜÏúºÎ©¥ Ï∂îÍ∞Ä
    wishlist.value.push(movie);
  } else {
    // ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏûàÏúºÎ©¥ Ï†úÍ±∞
    wishlist.value = wishlist.value.filter(item => item.id !== movie.id);
  }
  saveWishlist();
}

// ÌäπÏ†ï ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏûàÎäîÏßÄ ÌôïÏù∏
export function isInWishlist(movieId) {
  return wishlist.value.some(item => item.id === movieId);
}

// ÌòÑÏû¨ ÏúÑÏãúÎ¶¨Ïä§Ìä∏Î•º Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
export function getCurrentWishlist() {
  return wishlist.value;
}

// ÏúÑÏãúÎ¶¨Ïä§Ìä∏Î•º Î°úÎìúÌïòÎäî Ìï®Ïàò Ìò∏Ï∂ú
loadWishlist();

// ÏúÑÏãúÎ¶¨Ïä§Ìä∏Î•º Ï∞∏Ï°∞Ìï† Ïàò ÏûàÎäî refÎ•º export
export { wishlist };
