const AuthService = {
  users: JSON.parse(localStorage.getItem("users")) || [],

  tryLogin(email, password) {
    return new Promise((resolve, reject) => {
      // ÏûÖÎ†•Ìïú Ïù¥Î©îÏùºÍ≥º ÎπÑÎ∞ÄÎ≤àÌò∏Î°ú ÏÇ¨Ïö©Ïûê Í≤ÄÏÉâ
      const user = this.users.find((u) => u.email === email && u.password === password);
      
      if (user) {
        // API ÌÇ§Î•º Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
        localStorage.setItem("TMDb-Key", user.apiKey);
        resolve({
          message: "Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ",
          apiKey: user.apiKey, // API ÌÇ§ Î∞òÌôò
        });
      } else {
        reject(new Error("ÏïÑÏù¥Îîî ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏûòÎ™ªÎêòÏóàÏäµÎãàÎã§."));
      }
    });
  },

  tryRegister(email, password) {
    return new Promise((resolve, reject) => {
      // Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î©îÏùºÏù∏ÏßÄ ÌôïÏù∏
      if (this.users.some((u) => u.email === email)) {
        reject(new Error("Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§."));
      } else {
        // ÏÉà ÏÇ¨Ïö©Ïûê Îì±Î°ù Î∞è ÎûúÎç§ API ÌÇ§ ÏÉùÏÑ±
        const apiKey = `key-${Math.random().toString(36).substr(2, 16)}`;
        const newUser = { email, password, apiKey };
        this.users.push(newUser);
        // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏóÖÎç∞Ïù¥Ìä∏Îêú ÏÇ¨Ïö©Ïûê Î™©Î°ù Ï†ÄÏû•
        localStorage.setItem("users", JSON.stringify(this.users));
        resolve({
          message: "ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ",
          apiKey: apiKey, // ÏÉàÎ°ú ÏÉùÏÑ±Îêú API ÌÇ§ Î∞òÌôò
        });
      }
    });
  },
};

export default AuthService;
